          <div class="dropdown">
            <p class="mainoption" @click="showDropdownHandler">{{Number.isInteger(activeFilter) ? (lang === "fr" ? `Rayon de ${activeFilter} km` : `Within ${activeFilter} km`) : activeFilter}} <span :class="`chevron ${showDropdown ? `up` : `down`}`">▼</span></p>
            <ul :style="showDropdown ? 'height: auto;' : 'height: 0%;'">
              <li @click="sortBy(`Date`)">Date</li>
              <li @click="sortBy(`Distance`)" :class="`${!your_location ? `disabled` : ``}`">Distance</li>
              <li @click="sortBy(10)" :class="`${!your_location ? `disabled` : ``}`">{{lang === "fr" ?  `Rayon de 10 km` : `Within 10 km`}}</li>
              <li @click="sortBy(20)" :class="`${!your_location ? `disabled` : ``}`">{{lang === "fr" ?  `Rayon de 20 km` : `Within 20 km`}}</li>
              <li @click="sortBy(50)" :class="`${!your_location ? `disabled` : ``}`">{{lang === "fr" ?  `Rayon de 50 km` : `Within 50 km`}}</li>
