          <h2 v-show="lang === 'fr'">TELECHARGEMENT...</h2> -->
          <h2 v-show="lang === 'en'" class="loadmorebutton" @click="loadMore">Load More</h2>
          <h2 v-show="lang === 'fr'">En télécharger plus</h2>
